"use strict";

var logInBtn = document.getElementById("log-in-button");
var loginModal = document.getElementById("login-form");
var registerModal = document.getElementById("register-form");
var login = document.getElementById("login");
var register = document.getElementById("register");
var loginModalClose = document.getElementById("login-form-close");
var registerModalClose = document.getElementById("register-form-close");
var lostPasswordModal = document.getElementById("forgotten-password");
var passwordModal = document.getElementById("lost-email");
var lostPasswordModalClose = document.getElementById("missing-email-form-close");
logInBtn.addEventListener("click", function () {
  modalVisible(loginModal);
});
loginModalClose.addEventListener("click", function () {
  modalHide(loginModal);
});
register.addEventListener("click", function () {
  modalHide(loginModal);
  modalVisible(registerModal);
});
login.addEventListener("click", function () {
  modalHide(registerModal);
  modalVisible(loginModal);
});
registerModalClose.addEventListener("click", function () {
  modalHide(loginModal);
  modalHide(registerModal);
});
lostPasswordModal.addEventListener("click", function () {
  modalVisible(passwordModal);
});
lostPasswordModalClose.addEventListener("click", function () {
  modalHide(passwordModal);
});

function modalVisible(e) {
  e.style.display = "flex";
}

function modalHide(e) {
  e.style.display = "none";
}